.container {
    max-width: 600px;
    margin-top: 10px;
    margin:50px auto 0;
    padding: 20px;
    background-color: #f4f4f4;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    font-size: 32px;
    font-weight: bold;
    color: #333;
  }
  
  h1 span {
    color: #007bff;
  }
  
  .internal-div {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  input[type="text"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    outline: none;
  }
  
  .URL-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: 0.2s all;
  }

  .URL-button:active{
    transform: scale(0.98);
  }
  
  .result-div {
    margin-bottom: 20px;
  }
  
  .copy-container {
    display: flex;
    align-items: center;
  }
  
  .copy-container input[type="text"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .copy-button {
    padding: 10px 20px;
    background-color: #28a745;
    color: #fff;
    border: none;
    border-radius: 4px;
    margin-left: 10px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .analytics-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
  }
  .analytics-button:hover, .URL-button:hover{
    background-color: #044892;
  }
  .copy-button:hover{
    background-color: #145c25;
  }
  .error-message {
    color: #dc3545;
    font-size: 14px;
    margin-bottom: 10px;
  }
  
  .analytics-result {
    margin-top: 20px;
  }
  
  .analytics-result p {
    margin-bottom: 10px;
  }
  @media only screen and (max-width: 768px) {
    .container {
      padding: 10px;
      margin-top: 30px;
    }
  
    h1 {
      font-size: 28px;
      margin-bottom: 15px;
    }
  
    .internal-div {
      margin-bottom: 15px;
    }
  
    input[type="text"],
    .URL-button,
    .copy-container input[type="text"],
    .copy-button,
    .analytics-button {
      font-size: 14px;
    }
  }
  
  